.component-header {
	position: absolute;
	color: white;
	padding: 0 20px;
	width: calc(100% - 40px);
	height: 100%;
}

.top {
	display: flex;
	flex-direction: row-reverse;
}

.bottom {
	position: absolute;
	display: flex;
	justify-content: space-between;
	width: calc(100% - 40px);
	bottom: 0px;
}